@font-face {
  font-family: "Montserrat";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/montserrat-regular.woff2") format("woff2"), url("../fonts/montserrat-regular.woff") format("woff");
}
@font-face {
  font-family: "Montserrat";
  font-weight: 500;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/montserrat-medium.woff2") format("woff2"), url("../fonts/montserrat-medium.woff") format("woff");
}
@font-face {
  font-family: "Montserrat";
  font-weight: 600;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/montserrat-semibold.woff2") format("woff2"), url("../fonts/montserrat-semibold.woff") format("woff");
}
@font-face {
  font-family: "Montserrat";
  font-weight: 700;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/montserrat-bold.woff2") format("woff2"), url("../fonts/montserrat-bold.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Montserrat", "Arial", sans-serif;
  color: #011c40;
  scroll-behavior: smooth;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
  display: flex;
  flex-direction: column;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.overlay {
  position: fixed;
  opacity: 0;
  visibility: hidden;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  cursor: pointer;
  background-color: rgba(0, 43, 102, 0.5);
  z-index: 1;
}

.overlay--active {
  opacity: 1;
  visibility: visible;
}

.container {
  max-width: 1366px;
  margin: 0 auto;
  padding: 0 103px;
  width: 100%;
}
@media (max-width: 1023px) {
  .container {
    padding: 0 45px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 15px;
  }
}

.btn {
  display: inline-flex;
  padding: 10px 20px;
  color: #ffffff;
  background-color: #f9fbfd;
  border: none;
  cursor: pointer;
  transition: opacity 0.3s ease;
}

.logo-link {
  width: 70px;
  height: 38px;
}

.logo-link__icon {
  fill: #f9fbfd;
}
.logo-link__icon:hover {
  opacity: 70%;
}

@media (max-width: 1023px) {
  .logo-link,
.logo-link__icon {
    width: 38px;
    height: 20px;
  }
}
@media (max-width: 767px) {
  .logo-link,
.logo-link__icon {
    width: 42px;
    height: 22px;
  }
  .logo-link--header,
.logo-link__icon--header {
    fill: #f9fbfd;
  }
}
.main-nav__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
  flex-wrap: wrap;
}

.main-nav__item {
  padding: 11px 0;
  cursor: pointer;
}
.main-nav__item:hover {
  opacity: 70%;
}

.main-nav__link {
  margin-left: 34px;
  line-height: 19.5px;
  font-weight: 500;
  color: #f9fbfd;
}

.main-nav__toggle {
  display: none;
}

@media (max-width: 1023px) {
  .main-nav__link {
    margin-left: 24px;
    font-weight: 500;
  }
  .main-nav__link--header {
    font-size: 14px;
    line-height: 17.07px;
  }
  .main-nav__link--footer {
    font-size: 12px;
    line-height: 14.63px;
  }
}
@media (max-width: 767px) {
  .main-nav--footer {
    display: block;
  }
  .main-nav__list {
    flex-direction: column;
    justify-content: flex-start;
    display: none;
  }
  .main-nav__list--header {
    display: none;
  }
  .main-nav__list--footer {
    display: block;
    padding-top: 12px;
  }
  .main-nav__link {
    margin-left: 0;
    font-size: 12px;
    line-height: 14.63px;
  }
  .main-nav__link--header {
    color: #011c40;
  }
  .main-nav__item {
    padding: 5px 0;
  }
  .main-nav__toggle {
    content: "";
    position: absolute;
    top: 12px;
    right: 5px;
    display: block;
    width: 44px;
    height: 40px;
    background-color: transparent;
    border: none;
    padding: 10px;
    cursor: pointer;
  }
  .main-nav__toggle:hover {
    opacity: 70%;
  }
  .main-nav__toggle--opened::before {
    content: "";
    top: 12px;
    right: 10px;
    position: absolute;
    width: 24px;
    height: 2px;
    background-color: #f9fbfd;
    box-shadow: 0 6px 0 0 #f9fbfd, 0 12px 0 0 #f9fbfd;
  }
  .main-nav__toggle--closed {
    right: 40px;
  }
  .main-nav__toggle--closed::before {
    content: "";
    position: absolute;
    top: 20px;
    right: 13px;
    width: 20px;
    height: 2px;
    background-color: #011c40;
    transform: rotate(45deg);
  }
  .main-nav__toggle--closed::after {
    content: "";
    position: absolute;
    top: 20px;
    right: 13px;
    width: 20px;
    height: 2px;
    background-color: #011c40;
    transform: rotate(135deg);
  }
}
.main-header {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 31px 103px 10px;
}

@media (max-width: 1023px) {
  .main-header {
    padding: 17px 45px 10px;
  }
}
@media (max-width: 767px) {
  .main-header {
    display: block;
    margin: 0;
    padding: 20px 40px 20px 15px;
    z-index: 2;
    transition: all 0.9s ease;
  }
  .main-header--opened {
    min-width: 295px;
    min-height: 568px;
    margin-left: 30px;
    background-color: #f9fbfd;
    z-index: 5;
  }
}
.main-container {
  margin-bottom: auto;
}

.promo {
  min-height: 650px;
  background-color: #e3ecf6;
  background-image: image-set(url("../img/desktop/promo-background.webp") 1x, url("../img/desktop/promo-background@2x.webp") 2x, url("../img/desktop/promo-background.jpg") 1x, url("../img/desktop/promo-background@2x.jpg") 2x);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center bottom;
  padding: 270px 0 287px 95px;
}
.promo h1 {
  font-size: 54px;
  line-height: 65.83px;
  font-weight: 700;
  color: #f9fbfd;
  filter: drop-shadow(0 4px 4px rgba(0, 0, 0, 0.25));
  margin: 0;
}
.promo p {
  font-size: 20px;
  line-height: 24.38px;
  color: #f9fbfd;
  filter: drop-shadow(0 4px 4px rgba(0, 0, 0, 0.25));
  margin: 11px 0 0;
}

@media (max-width: 1023px) {
  .promo {
    min-height: 638px;
    background-image: image-set(url("../img/tablet/promo-background.webp") 1x, url("../img/tablet/promo-background@2x.webp") 2x, url("../img/tablet/promo-background.jpg") 1x, url("../img/tablet/promo-background@2x.jpg") 2x);
    padding: 267px 0 150px;
    filter: none;
  }
  .promo h1 {
    font-size: 50px;
    line-height: 60.95px;
    filter: none;
  }
  .promo p {
    font-size: 18px;
    line-height: 21.94px;
    filter: none;
  }
}
@media (max-width: 767px) {
  .promo {
    min-height: 500px;
    background-image: image-set(url("../img/mobile/promo-background.webp") 1x, url("../img/mobile/promo-background@2x.webp") 2x, url("../img/mobile/promo-background.jpg") 1x, url("../img/mobile/promo-background@2x.jpg") 2x);
    padding: 175px 0 90px 0;
  }
  .promo h1 {
    font-size: 36px;
    line-height: 45px;
    font-weight: 700;
  }
  .promo p {
    font-size: 16px;
    line-height: 22.4px;
    margin: 10px 0 0;
  }
}
.travel {
  padding: 111px 0 145px;
}

.travel__wrapper {
  display: grid;
  grid-template-columns: 1fr 1.09fr;
  gap: 80px;
  justify-content: space-between;
  align-items: center;
}

.travel__description h2 {
  font-size: 28px;
  line-height: 42px;
  font-weight: 600;
  margin: 0 0 23px;
  align-items: stretch;
}
.travel__description p {
  line-height: 24px;
  margin: 0;
}

@media (max-width: 1023px) {
  .travel {
    padding: 100px 0;
  }
  .travel__wrapper {
    grid-template-columns: 1fr 1fr;
    gap: 25px;
    align-items: flex-start;
  }
  .travel__description {
    margin: -10px 0 -7px;
    padding-right: 5px;
  }
  .travel__description h2 {
    font-size: 26px;
    line-height: 36.4px;
    margin: 0 0 16px;
  }
  .travel__image img {
    width: 324px;
    height: 174px;
  }
}
@media (max-width: 767px) {
  .travel {
    padding: 66px 0 88px;
  }
  .travel__wrapper {
    grid-template-columns: 1fr;
    gap: 50px;
    justify-items: center;
  }
  .travel__description h2 {
    font-size: 24px;
    line-height: 33.6px;
  }
  .travel__image img {
    width: 290px;
    height: 155px;
  }
}
.advantages {
  padding: 110px 0;
  position: relative;
  background-color: #e3ecf6;
  z-index: -2;
}
.advantages h2 {
  font-size: 28px;
  line-height: 42px;
  font-weight: 600;
  margin: 0;
}

.advantages__wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.advantages__list {
  margin: 0;
  padding: 50px 0 11px;
  list-style-type: none;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 40px;
}

.advantages__item {
  display: flex;
  flex-direction: column;
  background-color: #f9fbfd;
  padding: 30px 30px 36px;
}
.advantages__item h3 {
  font-size: 18px;
  line-height: 27px;
  font-weight: 500;
  margin-bottom: 0;
}
.advantages__item p {
  font-size: 14px;
  line-height: 19.6px;
  margin: 9px 0;
}

.advantages__image img {
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
  width: 100%;
  height: 100%;
}

@media (max-width: 1023px) {
  .advantages {
    padding: 90px 0;
  }
  .advantages h2 {
    font-size: 26px;
    line-height: 39px;
  }
  .advantages__list {
    gap: 30px;
    padding: 50px 0 9px;
  }
  .advantages__item {
    padding: 26px 15px 15px 20px;
  }
  .advantages__item h3 {
    line-height: 25.2px;
  }
  .advantages__icon {
    height: 26px;
  }
  .advantages__icon--cruise-ship {
    width: 21px;
  }
  .advantages__icon--all-inclusive {
    width: 27px;
  }
  .advantages__icon--zodiac {
    width: 36px;
  }
}
@media (max-width: 767px) {
  .advantages {
    padding: 55px 0 65px;
  }
  .advantages h2 {
    font-size: 24px;
    line-height: 36px;
  }
  .advantages__list {
    grid-template-columns: 1fr;
    gap: 30px;
    padding: 31px 0 0;
  }
  .advantages__item {
    padding: 26px 15px 13px 20px;
  }
  .advantages__item h3 {
    line-height: 25.2px;
    padding-right: 70px;
  }
}
.cruises {
  padding: 85px 0 120px;
}
.cruises h2 {
  font-size: 28px;
  line-height: 42px;
  font-weight: 600;
  margin-top: 0;
  margin-bottom: 49px;
}

.cruises__wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.cruises__list {
  margin: 0;
  padding: 0 15px;
  list-style-type: none;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(240px, 1fr));
  gap: 30px;
  width: 100%;
}

.cruises__item {
  position: relative;
