  font-size: 18px;
  line-height: 27px;
  font-weight: 500;
  margin: 0;
  margin-top: auto;
}
.cruises__item a {
  font-size: 14px;
  line-height: 21px;
  font-weight: 600;
  background-color: #f9fbfd;
