  justify-content: space-between;
  z-index: 2;
  width: 100%;
  height: 230px;
  overflow: auto;
}
.cruises__info-list ul {
  padding: 0;
}
.cruises__info-list li {
  margin-bottom: 11px;
}
.cruises__info-list h5 {
  font-size: 14px;
  line-height: 19.6px;
  font-weight: 500;
  color: #f9fbfd;
  margin: 0;
  padding: 0 0 2px;
}
.cruises__info-list p {
  font-size: 14px;
  line-height: 19.6px;
  font-weight: 400;
  color: #d8d8d9;
  margin: 0;
}

.cruises__info-image img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
  object-fit: cover;
}

.cruises__item:hover {
  padding: 29px 25px 30px;
}
.cruises__item:hover .cruises__info-image {
  display: none;
}
.cruises__item:hover h3 {
  margin: 0;
  margin-bottom: auto;
}
.cruises__item:hover a {
  display: block;
}

.cruises__item:hover .cruises__info-list {
  display: flex;
}

@media (max-width: 1023px) {
  .cruises {
    padding: 90px 0 100px;
