.form {
  display: flex;
  flex-direction: column;
  color: #011c40;
  background-color: #011c40;
  filter: drop-shadow(0 4px 45px rgba(1, 28, 64, 0.15));
  padding: 43px 50px 50px;
}
.form h3 {
  font-size: 18px;
  line-height: 27px;
  font-weight: 500;
  color: #f9fbfd;
  margin: 0 0 28px;
}
.form input {
  border: none;
  background-color: #f9fbfd;
  padding: 12px 16px;
  margin-bottom: 20px;
}
.form input::placeholder {
  font-size: 14px;
  line-height: 21px;
  opacity: 70%;
}
.form input:hover {
  background-color: #e3ecf6;
}

.form__agreement {
  align-self: flex-start;
  margin-bottom: 30px;
}
.form__agreement label {
  display: flex;
  align-items: center;
  flex-direction: row-reverse;
  font-size: 10px;
  line-height: 13px;
  color: #f9fbfd;
  cursor: pointer;
}
.form__agreement label:hover {
  opacity: 70%;
}
.form__agreement span {
  width: 18px;
  height: 18px;
  background-color: #f9fbfd;
  margin-right: 5px;
  cursor: pointer;
  position: relative;
}
.form__agreement input[type=checkbox]:checked + span::before {
  content: "";
  position: absolute;
  width: 1px;
  height: 6px;
  top: 42%;
  left: 25%;
  background-color: #011c40;
  transform: rotate(145deg);
}
.form__agreement input[type=checkbox]:checked + span::after {
  content: "";
  position: absolute;
  width: 1px;
  height: 10px;
  top: 22%;
  right: 38%;
  background-color: #011c40;
  transform: rotate(45deg);
}

.form__button {
  display: flex;
  justify-content: center;
  color: #011c40;
  font-size: 14px;
  line-height: 21px;
  font-weight: 600;
  text-align: center;
  padding: 10px 10px 12px;
}
.form__button:hover {
  background-color: #e3ecf6;
}

@media (max-width: 767px) {
