    margin-top: -3px;
    margin-bottom: 27px;
  }
  .form__agreement span {
    width: 20px;
    height: 20px;
    padding-right: 20px;
    margin-right: 8px;
  }
  .form__agreement input[type=checkbox]:checked + span::before {
    content: "";
    position: absolute;
    width: 1px;
    height: 6px;
    top: 45%;
    left: 34%;
    background-color: #011c40;
    transform: rotate(145deg);
  }
  .form__agreement input[type=checkbox]:checked + span::after {
